:root {
  $color-text: #cccccc;
  $color-link: #8374eb;
  $color-pink: #e874eb;
  $color-green: #74ec95;
  $color-orange: #eca474;
  $color-text-dark: #0c0c0c;
  $color-text-background: #ecd555;
  $color-foreground: #cccccc;
  $color-background-dark: #0c0c0c;
  $color-background: #ecd555;
  --color-text: #{$color-text};
  --color-text-dark: #{$color-text-dark};
  --color-link: #{$color-link};
  --color-link-hover: #{darken($color-link, 25%)};
  --color-text-background: #{$color-text-background};
  --color-foreground: #{$color-foreground};
  --color-foreground-rgb: rgb($color-foreground);
  --color-background: #{$color-background};
  --color-background-rgb: 236, 213, 85;
  --color-background-dark: #{$color-background-dark};
  --hero-background-color: #0c0c0c;
  --font-family: 'Roboto', 'Montserrat', -apple-system, BlinkMacSystemFont,
    'Segoe UI', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
    'Helvetica Neue', sans-serif;
}
